import React, { useState } from "react";
import UserTable from "./tables/UserTable";
import AddUserForm from "./tables/AddUserForm";
import EditUserForm from "./forms/EditUserForm"


const App = () => {
  const userData = [
    { id: 1, name: "Muhammad Zulhaditya", username: "Zulhaditya" },
    { id: 2, name: "Inayah Wulandari", username: "Inayah" },
  ];

  const addUser = (user) => {
    user.id = user.length + 1
    setUsers([...users, user])
  }

  const deleteUser = (id) => {
    setUsers(users.filter((user) => user.id !== id))
  }

  const [editing, setEditing] = useState(false)
  const initialFormState = {id: null, name: '', username: ''}
  const [currentUser, setCurrentUser] = useState(initialFormState)
  const editRow = (user) => {
    setEditing(true)

    setCurrentUser({id: user.id, name: user.name, username: user.username})
  }

  const updateUser = (id, updatedUser) => {
    setEditing(false)

    setUsers(users.map((user) => (user.id === id ? updatedUser : user)))
  }

  const [users, setUsers] = useState(userData)

  return (
    <div className="container">
      <h1>CRUD Apps dengan Hooks</h1>
      <div className="flex-row">
        <div className="flex-large">
          {editing ? (
            <div>
              <h2>Edit Customer</h2>
              <EditUserForm
                setEditing={setEditing}
                currentUser={currentUser}
                updateUser={updateUser}
              />
            </div>
          ) : (
            <div>
              <h2>Tambahkan Customer</h2>
              <AddUserForm addUser={addUser} />
            </div>
          )}
        </div>
        <div className="flex-large">
          <h2>Lihat Customer</h2>
          <UserTable users={users} editRow={editRow} deleteUser={deleteUser} />
        </div>
      </div>
    </div>
  );
};

export default App;
